<template>
  <div class="paynow">
  	<h2><i onclick="window.history.go(-1)"></i>立即支付</h2>
    <div class="paynow_top">
      <img src="../../static/img/payment_tips_icon@3x.png" alt="">
      <p>您的订单提交成功，为了不让到手的宝贝飞走，请在<em>30分钟</em>内完成订单支付哟</p>
    </div>
    <div class="payment_info">
      <p>支付信息</p>
      <p>
        <span>待付金额</span>
        <span>¥{{create_order.payFeeCent|capitalize}}</span>
      </p>
      <p>支付倒计时 <span>{{expireTime|countDown}}</span></p>
    </div>
    <div class="payment_mode">
      <h3>支付方式</h3>
      <ul>
        <li v-for="(item,index) in payment_type" v-bind:data-index="index" @click='payment_type_fun($event)'>
          <img v-if="index==0" src="../../static/img/order_setting_payment_alipay@3x.png" alt="">
          <img v-else-if="index==1" src="../../static/img/order_setting_payment_wechat_pay@3x.png" alt="">
          <img v-else-if="index==2" src="../../static/img/yizhifu_icon@3x.png" alt="">
          <span>{{item.name}}</span>
          <em  :class="{active:item.isActive}" ></em>
        </li>
      </ul>
    </div>
    <div id="paynow_btn" @click="paynow">
      立即支付
    </div>

    <!-- 分期mask -->
    <div class="mask">
         <div class="stagesMask">
          <h3>请选择分期</h3>
          <ul>
            <li>
              <div>
                <p>500元 X 3期</p>
                <p>手续费9.9元 / 期</p>
              </div>
              <em class="active"></em>
            </li>
            <li>
              <div>
                <p>200元 X 6期</p>
                <p>手续费5.9元 / 期</p>
              </div>
              <em></em>
            </li>
            <li>
              <div>
                <p>100元 X 12期</p>
                <p>手续费3.9元 / 期，费率8.3%</p>
              </div>
              <em></em>
            </li>
          </ul>
          <div id="stages_sure" @click="stages_sure">确定</div>
        </div>
    </div>
    <form id="alipayExtend"  name="punchout_form" method="post">
      <input type="hidden"  v-for="item in alipay" v-model="item.value" v-bind:name="item.name">
    </form>
    <form id="wechatExtend" method="post">
    </form>
    <form id="bestExtend" method="post"></form>
  </div>
</template>

<script>
import { Toast } from 'mint-ui';
import axios from 'axios'
export default {
  name: 'Paynow',
  data () {
    return {
      create_order:{},
      expireTime:'',
      timer:null,
      payment_type:[],
      payInfo:{},
      alipay:[],
    }
  },
  created(){
    //判断创建订单信息
    this.create_order = this.create_order_info.payFeeCent ? this.create_order_info : JSON.parse(localStorage.create_order_info);
    //计算目前倒计时时间
    //兼容safari浏览器
    this.expireTime = (new Date(this.create_order.payExpireTime.replace(/-/g,'/'))) - (new Date());
    this.payment_type = [
        {
          'name'  :'支付宝',
          'isActive':true
        },
        {
          'name'  :'微信',
          'isActive':false
        },
        {
          'name'  :'翼支付',
          'isActive':false
        },
        // {
        //   'imgurl':'../../static/img/huabei_icon@3x.png',
        //   'name'  :'花呗分期',
        //   'isActive':false
        // }
      ];
    console.log(this.assetsUrl);
  },
  computed:{
      create_order_info () {
        return this.$store.state.create_order_info
      }
  },
  methods:{
    paynow(){
      let obj = {
        'payType':1,
        'payFeeCent':this.create_order.payFeeCent,
        'orderIds':this.create_order.orderIds
      }
      this.payment_type.forEach((item,index)=>{
        if(item.isActive){
          // console.log(item.name,index+1);
          obj.payType = index+1
        }
      })
      // console.log(obj)
      this.$set(this,'payInfo',obj);
      this.$nextTick(()=>{
        this.alipay_ajax(this.payInfo);
      })
      
      // this.$router.push({
      //   path:'/payment_success'
      // })
    },
    stages_sure(){
      console.log('stages_sure');
    },
    payment_type_fun(event){
      this.payment_type.forEach((item,index)=>{

        if(event.currentTarget.dataset.index==index){
          item.isActive = true;
        }else{
          item.isActive = false;
        }
      })
    },
    alipay_ajax(obj){
      console.log(obj)
      axios.post(this.$api+'/h5/orders/pay',obj).then((res)=>{
      // axios.post('http://192.168.0.110:8080/h5/orders/pay',obj).then((res)=>{
        console.log(res.data.value);
        if(res.data.value.alipayExtend){
          //支付宝
          let arr = [],
              formFields= res.data.value.alipayExtend.formFields;
          let alipayForm = document.getElementById('alipayExtend');
          alipayForm.setAttribute('action',res.data.value.alipayExtend.actionUrl);

          for(var key in formFields){
              arr.push({
                'name':key,
                'value':formFields[key]
              })
          }
          this.$set(this,'alipay',arr)
          this.$nextTick(()=>{
            alipayForm.submit();
          })
        }else if(res.data.value.wechatExtend){
          //微信
            let wechatForm = document.getElementById('wechatExtend');
            wechatForm.setAttribute('action',res.data.value.wechatExtend.actionUrl);
            this.$nextTick(()=>{
              wechatForm.submit();
            })
        }else if(res.data.value.bestPayExtend){
          let bestForm = document.getElementById('bestExtend');
            bestForm.setAttribute('action',res.data.value.bestPayExtend.actionUrl);
            this.$nextTick(()=>{
              bestForm.submit();
            })
        }

      }).catch(error=>{
          let instance = Toast('网络好像有问题,请稍候再试试');
        setTimeout(() => {
          instance.close();
        }, 2000);
        })
    }
  },
  filters: {
    capitalize (value) {
      if (!value) return '0'
      return parseFloat(value/100).toFixed(2)
    },
    countDown(value){
      let second = parseInt(value/1000)%60,
          min    = (parseInt(value/1000)-second)/60%60,
          hour   = ((parseInt(value/1000)-second)/60-min)/60%24;
        return (hour<10?'0'+hour:hour)+':'+(min<10?'0'+min:min)+':'+(second<10?'0'+second:second);
    }
    
  },
  mounted(){
    this.timer = setInterval(()=>{
      if(this.expireTime>1000){
        this.$set(this,'expireTime',this.expireTime-1000)
      }else{
        this.$set(this,'expireTime',0);
        clearInterval(this.timer);
      }
    },1000)
  },
  destroyed(){
    clearInterval(this.timer)
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
@import '../common/css/var.scss';
.paynow{
  h2{
    height: 44px;
    line-height: 44px;
    display: flex;
    align-items:center;
    padding:0 20px;
    font-size:18px;
    color:#212121;
    font-family:PingFangSC-Regular;
    border-bottom: 1px solid #f4f4f4;/*no*/
    justify-content:center;
    position: relative;
    i{
      position: absolute;
      left: 20px;
      top:10px;
      width: 24px;
      height: 24px;
      background: url('../assets/order_navbar_arrow_back_icon@3x.png');
      background-size: 100%;
      margin-right: 10px;
    }
  }
  .paynow_top{
    max-width: 414px;
    margin:0 auto;
      width: 100%;
      height: 142px;
      display: flex;
      justify-content:space-between;
      align-items:center;
      padding:0 20px;
      box-sizing:border-box;
      border-bottom:10px solid $backgroundColor;
      img{
        width: 100px;
        height: 100px;
      }
      p{
        flex:1;
        font-family:PingFangSC-Thin;
        padding-left: 20px;
        font-size: 12px;
        color: $fontColor;
        line-height: 20px; 
        em{
          font-family:$fontFamily;
          font-size: 13px;
          color: black;
        }
      }
  }
  .payment_info{
    max-width: 414px;
    margin:0 auto;
    width: 100%;
    box-sizing:border-box;
    padding: 0 20px;
    border-bottom: 10px solid $backgroundColor;
    p:nth-child(1){
      height: 44px;
      line-height: 44px;
      font-family:PingFangSC-Medium;
      font-size:20px;
      color:$fontColor;
    }
    p:nth-child(2){
      display: flex;
      font-size: 14px;
      height: 54px;
      line-height: 54px;
      justify-content:space-between;
      border-bottom: 1px solid #f4f4f4;/*no*/
    }
    p:nth-child(3){
      font-family:PingFangSC-Thin;
      height: 54px;
      line-height: 54px;
      text-align: right;
      font-size: 20px;
      color: $fontColor;
      span{
        font-weight: bold;
      }
    }
  }
  .payment_mode{
    max-width: 414px;
    margin:0 auto;
    width: 100%;
    padding: 0 20px 48px;
    box-sizing:border-box;
    h3{
      height: 44px;
      line-height: 44px;
      font-family:PingFangSC-Medium;
      font-size:20px;
      color:$fontColor;
    }
    ul{
      li{
        display: flex;
        justify-content:space-between;
        align-items:center;
        height: 44px;
        border-bottom: 1px solid #f4f4f4;/*no*/
        img{
          width: 24px;
          height: 24px;
        }
        span{
          flex:1;
          padding: 0 10px;
          font-size:14px;
          color:rgba(0,0,0,0.87);
        }
        em{
            width: 18px;
            height: 18px;
            background: url('../assets/payment_uncheck_common_icon@3x.png');
            background-size: 100%;
        }
        em.active{
          background: url('../assets/payment_check_common_icon@3x.png');
          background-size: 100%;
        }
      }
    }
  }
  #paynow_btn{
    width: 100%;
    height: 48px;
    position: fixed;
    bottom: 0;
    left: 0;
    background: $pink;
    text-align: center;
    line-height: 48px;
    color: white;
    font-family:PingFangSC-Regular;
    font-size:18px;
    transform: translate3d(0,0,0);
  }
}
.mask{
  display: none;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  background: rgba(0,0,0,0.5);
  transform: translate3d(0,0,0);
  .stagesMask{
    position: absolute;
    bottom: 0;
    left: 0;
    background: white;
    width: 100%;
    font-size: 16px;
    color:#737373;
    font-family:PingFangSC-Regular;
    h3{
      height: 44px;
      line-height: 44px;
      padding: 0 20px;
    }
    ul{
      padding: 0 20px 25px;
      li{
        height: 54px;
        border-bottom: 1px solid #f4f4f4;/*no*/
        box-sizing:border-box;
        padding: 11px 0;
        display: flex;
        align-items:center;
        justify-content:space-between;
        div{
          p:nth-child(1){
            font-size: 14px;
            padding-bottom: 10px;
            color: $fontColor;
          }
          p:nth-child(2){
            font-size: 12px;
          }
        }
        em{
          width: 18px;
          height: 18px;
          background: url('../assets/payment_uncheck_common_icon@3x.png');
          background-size: 100%;
        }
        em.active{
          background: url('../assets/payment_check_common_icon@3x.png');
          background-size: 100%;
        }
      }
    }
    #stages_sure{
      width: 100%;
      height: 48px;
      line-height: 48px;
      text-align: center;
      background: $pink;
      color:white;
      font-size: 18px;
    }
  }
}
</style>



